import { useForm, SubmitHandler, FieldValues } from 'react-hook-form';
import './style/TitleSecond.scss';
import { Button } from '../../../../components/UI/Button/Button';
import { Title } from '../../../../components/';
import { Input } from '../../../../components';

export const TitleSecond = () => {
  const {
    register,
    formState: { errors },
    handleSubmit,
    setValue,
  } = useForm({ mode: 'onBlur' });

  const onSubmit: SubmitHandler<FieldValues> = (data) => {
    //  console.log(data);
    ('');
  };

  return (
    <section className='groupTraining'>
      <Title name='Program' />
      <div className='container'>
        <div className='groupTraining__table'>
          <div className='groupTraining__row'>
            <h2 className='groupTraining__text'>
              Sign up for a trial workout!
            </h2>
          </div>

          <div className='firstTrain'>
            <form
              onSubmit={handleSubmit(onSubmit)}
              className='firstTrain__form'
            >
              <div className='firstTrain__input'>
                <Input
                  req={true}
                  register={register}
                  type='text'
                  name='number'
                  textFiled='Number'
                  errors={errors}
                  setValue={setValue}
                />
              </div>
              <div className='firstTrain__btn'>
                <Button name='sign'>
                  <p className='mini'>Send</p>
                </Button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};
