import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Parallax } from 'swiper';
import 'swiper/css';
import 'swiper/css/navigation';
import React, { useEffect } from 'react';
import './Style/Slider.scss';
import { $ } from '../../../../components/UX/customeJQ/customeJQ';

export default function SliderGroup() {
  const handlerSize = () => {
    const container =
      (+document.documentElement.clientWidth -
        $('.container').take().clientWidth) /
      2;

    $('.swiper-button-prev').take().style.width = container + 'px';
    $('.swiper-button-next').take().style.width = container + 'px';

    if (document.documentElement.clientWidth > 756) {
      $('.mySwiper .swiper-slide').take().style.paddingLeft = container + 'px';
      $('.mySwiper .swiper-slide').take().style.paddingRight = container + 'px';
    } else {
      $('.mySwiper .swiper-slide').take().style.paddingLeft = '';
      $('.mySwiper .swiper-slide').take().style.paddingRight = '';
    }
  };

  useEffect(() => {
    handlerSize();
    window.addEventListener('resize', handlerSize);
    window.addEventListener('click', handlerSize);
    return function cleanup() {
      window.removeEventListener('resize', handlerSize);
      window.removeEventListener('click', handlerSize);
    };
  }, []);

  return (
    <>
      <Swiper
        speed={600}
        parallax={true}
        modules={[Parallax, Navigation]}
        className='mySwiper'
        navigation={{
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        }}
      >
        <div
          slot='container-start'
          className='parallax-bg'
          data-swiper-parallax='-23%'
        ></div>
        <SwiperSlide>
          <div className='title' data-swiper-parallax='-300'>
            <h1>Kids workout</h1>
          </div>
          <div className='subtitle' data-swiper-parallax='-200'>
            <h3>Program</h3>
          </div>
          <div className='text' data-swiper-parallax='-100'>
            <p className='mini'>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam
              dictum mattis velit, sit amet faucibus felis iaculis nec.
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className='title' data-swiper-parallax='-300'>
            <h1>Group</h1>
          </div>
          <div className='subtitle' data-swiper-parallax='-200'>
            <h3>Program</h3>
          </div>
          <div className='text' data-swiper-parallax='-100'>
            <p className='mini'>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam
              dictum mattis velit, sit amet faucibus felis iaculis nec.
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className='title' data-swiper-parallax='-300'>
            <h1>Group</h1>
          </div>
          <div className='subtitle' data-swiper-parallax='-200'>
            <h3>Program</h3>
          </div>
          <div className='text' data-swiper-parallax='-100'>
            <p className='mini'>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam
              dictum mattis velit, sit amet faucibus felis iaculis nec.
            </p>
          </div>
        </SwiperSlide>
        <div className='swiper-button-prev'></div>
        <div className='swiper-button-next'></div>
      </Swiper>
    </>
  );
}
