import { useEffect } from 'react';
import { $ } from '../../../../components/UX/customeJQ/customeJQ';
import './style/InfoBlock.scss';

export const InfoBlock = () => {
  useEffect(() => {
    window.addEventListener('scroll', scrollDoc);
    return () => {
      window.removeEventListener('scroll', scrollDoc);
    };
  }, []);

  return (
    <section className='infoblock indent'>
      <div className='container'>
        <div className='infoblock__items'>
          <div className='infoblock__item scroll' data-block='true'>
            <div className='infoblock__block1 '>
              <div className='infoblock__back one'>
                <div className='infoblock__img'></div>
              </div>
            </div>
            <div className='infoblock__block2'>
              <div className='infoblock__body'>
                <h2>boxing</h2>
                <p>
                  Lorem, ipsum dolor sit amet consectetur adipisicing elit.
                  Voluptas quos natus aut laudantium tempore iusto error
                  mollitia hic dolore cupiditate asperiores facilis, excepturi
                  ea architecto illo magnam qui maiores velit.
                </p>
              </div>
            </div>
          </div>
          <div className='infoblock__item scroll' data-block='true'>
            <div className='infoblock__block2'>
              <div className='infoblock__body'>
                <h2>gym</h2>
                <p>
                  Lorem, ipsum dolor sit amet consectetur adipisicing elit.
                  Voluptas quos natus aut laudantium tempore iusto error
                  mollitia hic dolore cupiditate asperiores facilis, excepturi
                  ea architecto illo magnam qui maiores velit.
                </p>
              </div>
            </div>
            <div className='infoblock__block1 '>
              <div className='infoblock__back two'>
                <div className='infoblock__img'></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

function scrollDoc() {
  const scroll = window.pageYOffset + document.documentElement.clientHeight;
  const blockTitle = document.querySelector(
    '.titleFirst__title'
  ) as HTMLElement;

  if (
    scroll >
    blockTitle.getBoundingClientRect().top + window.pageYOffset + 200
  ) {
    blockTitle.style.transform = 'translateX(' + 0 + '%)';
  }
}
