import { useEffect } from 'react';
import { Banner } from './components/Banner/Banner';
import { GroupTrain } from './components/GroupTrain/GroupTrain';
import { InfoBlock } from './components/InfoBlock/InfoBlock';
import OurGoods from './components/Shop/OurGoods';
import { TitleFirst } from './components/TitleFirst/TitleFirst';
import { TitleSecond } from './components/TitleSecond/TitleSecond';

export default function Home() {
  useEffect(() => {
    document.title = 'Home';
  }, []);

  return (
    <>
      <Banner />
      <TitleFirst />
      <InfoBlock />
      <TitleSecond />
      <GroupTrain />
      <OurGoods />
    </>
  );
}
