import { $ } from '../../../../components/UX/customeJQ/customeJQ';
import './style/Banner.scss';
import img from '../../../../img/Home/BannerHead.jpg';

export const Banner = () => {
  return (
    <section className='banner load'>
      <div className='banner__img '>
        <div className='banner__load'></div>
        <img
          src={img}
          onLoad={(e) => handleImageLoaded(e, img, '.banner__img', '.banner')}
        />
      </div>
      <div className='banner__ground '>
        <div className='container'>
          <div className='banner__menu'>
            <div className='banner__body'>
              <h2 translate='no' className='banner__title'>
                <span>Train</span> <span>hard and</span>
                <span>get results</span>
              </h2>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export const handleImageLoaded = (
  e: React.SyntheticEvent<HTMLDivElement>,
  img,
  elemBg: string,
  elem: string
) => {
  const target = e.target as HTMLElement;
  $(
    target.closest(elemBg) as HTMLElement
  ).take().style.backgroundImage = `url(${img})`;

  $(target.closest(elem) as HTMLElement).remove('load');
  $('.banner__title').add('end');
  target.remove();
};
