import './Style/GroupTrain.scss';
import SliderGroup from './SliderGroup';
import { BtnArrow, Wrapper } from '../../../../components/UI/Button/Button';

export const GroupTrain = () => {
  const ButtonWrap = Wrapper(BtnArrow);

  return (
    <section className='grouptrain indent'>
      <SliderGroup />

      <div className='lookmore__background'>
        <div className='container'>
          <div className='lookmore'>
            <p className='lookmore__text'>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Facere
              praesentium, impedit qui consequatur et vero ea! Error est
              quisquam, natus possimus voluptas nihil quae atque neque ducimus
              non exercitationem soluta?
            </p>
            <div className='lookmore__btn'>
              <ButtonWrap router='/schedule'>Look more</ButtonWrap>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
